import React, { Component } from "react";
import jsPDF from "jspdf";
import "jspdf-autotable";

class PdfButton extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pageWidth: 210,
      pageCenter: 105,
      padding: 30,
      headerY: 30,
      tipsY: 52,
      bodyFontSize: 10,
      tablePadding: 5,
      black: [59, 73, 86]
    };
  }
  createPdf = () => {
    const doc = new jsPDF({
      orientation: "p",
      unit: "mm",
      format: "A4"
    });

    // Header
    let title;
    let subtitleTxt;
    if (this.props.data.result.usage === "over") {
      title = "Vups! Du ligger vist i den i hje ende";
      subtitleTxt =
        "Det er ser ud til, at du bruger lidt mere strm end gennemsnittet. Med et par sm fifs kan vi sammen mske ndre lidt p det ?";
    } else {
      title = "Sdan! Du er en haj til det med strm";
      subtitleTxt =
        "Det er ser ud til, at du bruger mindre strm end gennemsnittet.Godt get! Mske er der alligevel et rd eller to, som en elhaj som dig kan hapse med.";
    }

    doc.setFont("helvetica");
    doc.setFontStyle("bold");
    doc.setTextColor(59, 73, 86);
    doc.setFontSize(22);
    doc.text(
      title,
      this.state.pageCenter,
      this.state.headerY,
      null,
      null,
      "center"
    );

    // Sub header
    var subtitle = doc
      .setFont("helvetica")
      .setTextColor(59, 73, 86)
      .setFontStyle("normal")
      .setFontSize(this.state.bodyFontSize)
      .splitTextToSize(
        subtitleTxt,
        this.state.pageWidth - 2 * this.state.padding
      );
    doc.text(
      subtitle,
      this.state.pageCenter,
      this.state.headerY + 10,
      null,
      null,
      "center"
    );

    doc.autoTable({
      margin: { top: this.state.tipsY },
      head: [
        [
          {
            content: "Her er de tre allerbedste rd til dig fra os",
            colSpan: 3
          }
        ]
      ],
      body: [
        [
          this.props.data.result.tips[0].tip.title,
          this.props.data.result.tips[1].tip.title,
          this.props.data.result.tips[2].tip.title
        ],
        [
          this.props.data.result.tips[0].tip.body,
          this.props.data.result.tips[1].tip.body,
          this.props.data.result.tips[2].tip.body
        ]
        // ...
      ],
      styles: {
        font: "helvetica",
        fillColor: [142, 205, 200],
        cellPadding: { top: 3, right: 5, bottom: 5, left: 5 },
        fontSize: this.state.bodyFontSize,
        cellWidth: this.state.pageWidth / 3 - 2 * this.state.tablePadding,
        textColor: this.state.black
      },
      alternateRowStyles: {
        font: "helvetica",
        fontStyle: "bold",
        fillColor: [142, 205, 200],
        cellPadding: { top: 0, right: 5, bottom: 0, left: 5 },
        fontSize: this.state.bodyFontSize,
        cellWidth: this.state.pageWidth / 3 - 2 * this.state.tablePadding,
        textColor: this.state.black
      },
      headStyles: {
        font: "helvetica",
        fontStyle: "bold",
        fillColor: [142, 205, 200],
        halign: "center",
        cellPadding: 10,
        fontSize: 14,
        textColor: this.state.black
      }
    });

    doc.save("orsted-eltjek.pdf");
  };
  render() {
    return (
      <div className="o-pdf-download o-btn o-red" onClick={this.createPdf}>
        PDF
      </div>
    );
  }
}

export default PdfButton;
